@use "../utilities/ui";

$select: ".slide" !default;

@forward "../adapters/vimeo-carousel";

@mixin new {
    #{$select} {
        $this: &;

        &__into {
            position: relative;

            display: flex;
            align-items: flex-start;
            margin: 0.25rem;
            border-color: #252525;
            contain: content;

            @include ui.scrollbar;
        }

        &__item {
            display: flex;
            align-items: center;
            flex: 1 0 100%;
            scroll-snap-align: start;
            position: relative;
        }

        img {
            display: block;
            object-fit: cover;
            // width: 100%;
            // height: 100%;
            height: auto;
            max-width: 100%;
        }

        & &__video {
            align-items: center;

            img {
                height: auto;
                bottom: 0;
                margin: auto;
            }
        }

        &__is-hidden {
            display: none;
        }

        &--vertical {
            #{$this}__into {
                flex-direction: column;

                overscroll-behavior-y: contain;
                scroll-snap-type: y mandatory;
                overflow-y: scroll;
                overflow-x: hidden;
            }
        }

        & &__into--no-scroll {
            overflow: hidden;
        }

        &__caption {
            position: absolute;
            bottom: 1em;
            left: 0;
            display: flex;
            justify-content: center;
            right: 0;

            &__text {
                background: rgba(0, 0, 0, 0.75);
                color: white;
                font-size: 1.1em;
                padding: 0.25em 1em;
                border-radius: 2em;
                text-align: center;
                min-width: 10em;
                box-shadow: 0 0.1em 0.5em rgba(0, 0, 0, 0.5);
            }
        }
    }
}
